<template>
  <div class="cart">
    <header>
      <h1>Your WishList</h1>
    </header>

    <div class="bookbox" v-if="bc.length !== 0">
        <div class="title">
          <div class="line"></div>
          <div>Books</div>
          <div class="line"></div>
        </div>
        <div class="bc">
          <div class="books" v-for="(item, index) in bc" :key="index">
            <div class="delete" @click="deletebook(item)">
              <svg
                t="1620013907000"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="2445"
                width="16"
                height="16"
              >
                <path
                  d="M975.069283 746.176494a162.756163 162.756163 0 0 1-230.134685 230.134686L511.97742 743.399162 279.065402 976.31118A162.756163 162.756163 0 0 1 48.930717 746.176494L281.842734 513.241896 48.930717 280.329879a162.688423 162.688423 0 0 1 0-230.112106 162.711003 162.711003 0 0 1 230.134685 0l232.912018 232.889438L744.912018 50.217773a162.711003 162.711003 0 1 1 230.134685 230.112106L742.134686 513.241896l232.934597 232.934598z"
                  fill="#13227a"
                  p-id="2446"
                ></path>
              </svg>
            </div>
            <div class="bookimage">
              <el-image :src="item.image" style="width: 100%; height: 100%">
                <div slot="placeholder" class="image-slot">
                  loading<span class="dot">...</span>
                </div>
              </el-image>
            </div>
            <div class="booktitle">
              <p>{{ item.name }}</p>
            </div>
            <div class="bookauthor">
              <p>{{ item.author }}</p>
            </div>
          </div>
        </div>
    </div>
    <div class="gamebox" v-if="gc.length !== 0">
        <div class="title">
          <div class="line"></div>
          <div>Game</div>
          <div class="line"></div>
        </div>
        <div class="gc">
          <div class="game" v-for="(item, index) in gc" :key="index">
            <div class="delete" @click="deletegame(item)">
              <svg
                t="1620013907000"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="2445"
                width="16"
                height="16"
              >
                <path
                  d="M975.069283 746.176494a162.756163 162.756163 0 0 1-230.134685 230.134686L511.97742 743.399162 279.065402 976.31118A162.756163 162.756163 0 0 1 48.930717 746.176494L281.842734 513.241896 48.930717 280.329879a162.688423 162.688423 0 0 1 0-230.112106 162.711003 162.711003 0 0 1 230.134685 0l232.912018 232.889438L744.912018 50.217773a162.711003 162.711003 0 1 1 230.134685 230.112106L742.134686 513.241896l232.934597 232.934598z"
                  fill="#13227a"
                  p-id="2446"
                ></path>
              </svg>
            </div>
            <div class="gameimage">
              <el-image :src="item.image" style="width: 100%; height: 100%">
                <div slot="placeholder" class="image-slot">
                  loading<span class="dot">...</span>
                </div>
              </el-image>
            </div>
            <div class="gametitle">
              <p>{{ item.name }}</p>
            </div>
            <div class="gamecost">
              <p>${{ item.cost }}</p>
            </div>
            <div class="gamecate">
              <p>{{ item.cate }}</p>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bc: [],
      gc: [],
    }
  },
  created() {
    let gameitem = this.$store.state.games;
    if (gameitem && Object.keys(gameitem).length !== 0) {
      Object.keys(gameitem).forEach((item) => {
        this.gc.push({
          name: item,
          cost: gameitem[item]["Cost in AUD"],
          cate: gameitem[item]["Category"],
          image: gameitem[item]["Image URL"],
        });
      });
    }
    let bookitem = this.$store.state.books;
    if (bookitem && Object.keys(bookitem).length !== 0) {
      Object.keys(bookitem).forEach((item) => {
        this.bc.push({
          name: item,
          image: bookitem[item].image,
          author: bookitem[item].author.join(", "),
        });
      });
    }
  },
  methods: {
    deletegame(item) {
      this.gc.splice(this.gc.indexOf(item), 1);
      delete this.$store.state.games[item.name];
      this.open1();
    },
    deletebook(item) {
      this.bc.splice(this.bc.indexOf(item), 1);
      delete this.$store.state.books[item.name];
      this.open1();
    },
    open1() {
      this.$notify({
        title: "Success",
        message: "Delete Successfully",
        type: "success",
        offset: 200,
      });
    },
  }
}

</script>
<style scoped>
.cart {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  max-width: 1300px;
  min-width: 1000px;
}
header {
  margin: 60px auto;
  text-align: center;
  font-size: 25px;
}
.title {
  position: relative;
  width: 500px;
  left: 50%;
  top: 30px;
  transform: translate(-50%);
  display: flex;
  align-items: center;
  font-size: 30px;
  text-align: center;
  margin-top: 20px;
  /* color: #ffffff; */
  color: #333;
  font-family: Arial, Helvetica, sans-serif;
}
.title .line {
  width: 95px;
  height: 2px;
  background-color: #cb980f;
  margin: auto auto;
}
.bc,
.ec,
.gc {
  margin-top: 50px;
}
.game {
  width: 80%;
  margin: 20px auto;
  border: 1px solid black;
  border-radius: 10px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.gameimg {
  width: 110px;
  height: 150px;
}
.gametitle {
  width: 150px;
}
.gamecost {
  width: 100px;
}
.gamecate {
  width: 150px;
}
.gameimage {
  width: 110px;
  height: 100%;
}
.books {
  width: 80%;
  margin: 20px auto;
  border: 1px solid black;
  border-radius: 10px;
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.books .bookimage {
  width: 110px;
  height: 150px;
}
.books .booktitle {
  width: 200px;
  text-align: center;
}
.books .bookauthor {
  width: 150px;
  text-align: center;
}
</style>